export class FieldDetails {
    static setDefinitions(definitions) {
        this.definitions = definitions;
        const keys = Object.keys(this.definitions);
        this.default = keys[0];
        for (let key of keys) {
            if (this.definitions[key].default) {
                this.default = key;
                break;
            }
        }
    }
}
FieldDetails.definitions = {};
